
.logo{
  margin: 8px;
  height: 65px;
  width: auto;
}

.image {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover
}

.gridContainer{
  height: 250px;
  position: relative;
  text-align: center;
  color: white;
}

.imageAuto {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    height: 300px;
    width: 300px;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: "center";
            justify-content: "center";
}

.video {
  display: block;
  width: 100%;
  height: auto;
  background: #008CBA
}

.details_container{
  margin: 32px;
}

.dummy{
  background: #008CBA
}

.basecolor{
  background: #0045D9
}

.divide64{
  height: 32px;
}

.divide32{
  height: 32px;
}

.gridColums{
  padding: 32px;
  height: 200px;
  background: #1B3058;
}

.gridColumsAchievement{
  padding: 32px;
  height: 80px;
  background: #1B3058;
}

.clientText{
  max-width: 400px;
}


.bottomNavLeft{
  height: 300px;
  background: #1B3058;
}

.bottomNavRight{
  height: 300px;
  background: #FFFFFF;
}

.grid{
  padding: 0;
  margin: 0;
}

.grid-container {
  display: grid;
  grid-template-columns: auto auto;
  background-color: #1B3058;
}

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.bottom-left {
  position: absolute;
  bottom: 16px;
  left: 16px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.home{
  background: #EEEDEE
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

